"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { MessageSquare, Image as ImageIcon } from "lucide-react";
import ChatMode from "@/components/ChatMode";
import ImageMode from "@/components/ImageMode";

export default function Home() {
  const [mode, setMode] = useState<"chat" | "image">("chat");

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 text-white p-4 md:p-8">
      <div className="max-w-6xl mx-auto space-y-8">
        {/* Header */}
        <div className="text-center space-y-4">
          <h1 className="text-4xl md:text-6xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-purple-400 to-pink-600">
            AI Assistant
          </h1>
          <p className="text-gray-400">Chat with AI or generate images using natural language</p>
        </div>

        {/* Mode Toggle */}
        <div className="flex justify-center gap-4">
          <Button
            variant={mode === "chat" ? "default" : "outline"}
            onClick={() => setMode("chat")}
            className="gap-2"
          >
            <MessageSquare className="w-4 h-4" />
            Chat Mode
          </Button>
          <Button
            variant={mode === "image" ? "default" : "outline"}
            onClick={() => setMode("image")}
            className="gap-2"
          >
            <ImageIcon className="w-4 h-4" />
            Image Mode
          </Button>
        </div>

        {/* Content Area */}
        <Card className="p-4 bg-gray-800/50 border-gray-700">
          <AnimatePresence mode="wait">
            <motion.div
              key={mode}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.2 }}
            >
              {mode === "chat" ? <ChatMode /> : <ImageMode />}
            </motion.div>
          </AnimatePresence>
        </Card>
      </div>
    </main>
  );
}
